import React, { Component } from 'react'
import './index.css'
import dataFormat from '../../handler/dataformat'
import fetchJsonp from 'fetch-jsonp'
import Item from '../item'

class List extends Component {
  constructor (props) {
    super(props)
    this.state = {
      api: this.props.api,
      apiArr: this.props.apiArr,
      apiItem: this.props.apiItem,
      urlLink: this.props.urlLink,
      keyword: this.props.keyword,
      detailapi: `https://api.douban.com/v2/${this.props.keyword}`
    }
    console.log('List api', this.state.api)
    console.log('List urlLink', this.state.urlLink)
  }
  // 渲染前首次拉取接口
  componentWillMount () {
    fetchJsonp(this.state.api)
      .then(response => {
        return response.json()
      }).then(data => {
        let item = dataFormat(this.state.apiItem, data)
        console.log('parsed data', data)
        console.log('parsed item', item)
        this.setState({
          apiArr: item
        })
      }).catch(ex => {
        console.log('parsing failed', ex)
      })
  }
  render () {
    const apiArr = this.state.apiArr
    return (
      <div>
        <div className='list'>
          <ul>
            {apiArr.map((item, index) => {
              return (
                <Item item={item} key={index} index={index} urlLink={this.state.urlLink} keyword={this.state.keyword} detailapi={this.state.detailapi} />
              )
            })}
          </ul>
        </div>
      </div>
    )
  }
  
  
  
  componentDidUpdate (prevProps, prevState) {
    // api 有更新则拉取接口
    console.log('List componentDidUpdate api', this.props.api)
    if (this.props.api !== prevState.api) {
      console.log('Component DID UPDATE!')
      fetchJsonp(this.state.api)
        .then(response => {
          return response.json()
        }).then(data => {
          let item = dataFormat(this.state.apiItem, data)
          console.log('parsed data', data)
          //this.setState({
          //  apiArr: item
          //})
        }).catch(ex => {
          console.log('parsing failed', ex)
        })
    }
  }
}

export default List



